import { initializeApp } from 'firebase/app';
import {
  getAuth,
  GoogleAuthProvider,
  onAuthStateChanged,
  signInWithPopup,
  signOut
} from 'firebase/auth';
import {
  addDoc,
  collection,
  deleteDoc,
  doc,
  getDoc,
  getDocs,
  getFirestore,
  onSnapshot,
  query,
  serverTimestamp,
  setDoc,
  updateDoc,
  where
} from 'firebase/firestore';
import { getDownloadURL, getStorage, ref, uploadBytes } from "firebase/storage";
import { AlignJustify, BarChart2, ChevronDown, ChevronsLeft, Clock, Edit, Eye, LayoutDashboard, List, Lock, LogOut, Moon, Plus, Search, Settings, Share2, Sun, Trash2, Trophy, UploadCloud, UserCheck, UserX, X, Zap } from 'lucide-react';
import React, { createContext, useContext, useEffect, useMemo, useRef, useState } from 'react';

// --- Konfigurasi Firebase ---
// Pastikan variabel lingkungan Anda telah diatur dengan benar di file .env
const firebaseConfig = {
    apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
    authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
    projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
    storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
    messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
    appId: process.env.REACT_APP_FIREBASE_APP_ID
};

const appId = process.env.REACT_APP_FIREBASE_PROJECT_ID || 'default-trading-journal';
const ADMIN_ID = process.env.REACT_APP_ADMIN_ID; 

// --- Inisialisasi Firebase ---
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const db = getFirestore(app);
const storage = getStorage(app);
const provider = new GoogleAuthProvider();

// --- Konteks Tema (Dark/Light Mode) ---
const ThemeContext = createContext();

const ThemeProvider = ({ children }) => {
    const [theme, setTheme] = useState(() => localStorage.getItem('theme') || 'dark');

    useEffect(() => {
        const root = window.document.documentElement;
        root.classList.remove(theme === 'dark' ? 'light' : 'dark');
        root.classList.add(theme);
        localStorage.setItem('theme', theme);
    }, [theme]);

    const toggleTheme = () => {
        setTheme(prevTheme => (prevTheme === 'dark' ? 'light' : 'dark'));
    };

    return (
        <ThemeContext.Provider value={{ theme, toggleTheme }}>
            {children}
        </ThemeContext.Provider>
    );
};

const useTheme = () => useContext(ThemeContext);

// --- Komponen Styling yang Dapat Digunakan Kembali ---
const Card = ({ children, className = '' }) => (
    <div className={`bg-white dark:bg-slate-800 rounded-xl shadow-md ${className}`}>
        {children}
    </div>
);

const Button = ({ children, onClick, variant = 'primary', className = '', ...props }) => {
    const baseClasses = 'inline-flex items-center justify-center px-4 py-2 text-sm font-semibold rounded-lg transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 dark:focus:ring-offset-slate-900 disabled:opacity-50 disabled:cursor-not-allowed';
    const variants = {
        primary: 'bg-blue-600 text-white hover:bg-blue-700 shadow-sm focus:ring-blue-500',
        secondary: 'bg-slate-200 dark:bg-slate-700 text-slate-800 dark:text-slate-200 hover:bg-slate-300 dark:hover:bg-slate-600 focus:ring-slate-500',
        danger: 'bg-red-600 text-white hover:bg-red-700 shadow-sm focus:ring-red-500',
        ghost: 'bg-transparent text-slate-500 hover:bg-slate-200 dark:hover:bg-slate-700 dark:text-slate-400',
    };
    return <button onClick={onClick} className={`${baseClasses} ${variants[variant]} ${className}`} {...props}>{children}</button>;
};

// --- Kelas Styling Global untuk Form ---
const labelClasses = "block text-sm font-medium text-slate-700 dark:text-slate-300 mb-1.5";
const inputClasses = "block w-full px-3 py-2 bg-white dark:bg-slate-700 text-slate-900 dark:text-slate-200 border border-slate-300 dark:border-slate-600 rounded-lg shadow-sm placeholder-slate-400 dark:placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 sm:text-sm transition";
const inputDisplayClasses = "block w-full px-3 py-2 bg-slate-100 dark:bg-slate-700/50 text-slate-600 dark:text-slate-400 border border-slate-200 dark:border-slate-600 rounded-lg sm:text-sm";


// --- Komponen Utama Aplikasi ---
export default function App() {
    return (
        <ThemeProvider>
            <MainApp />
        </ThemeProvider>
    );
}

function MainApp() {
    const [user, setUser] = useState(null);
    const [isAdmin, setIsAdmin] = useState(false);
    const [accountStatus, setAccountStatus] = useState('loading');
    const [loadingAuth, setLoadingAuth] = useState(true);

    useEffect(() => {
        const unsubscribe = onAuthStateChanged(auth, async (currentUser) => {
            if (currentUser) {
                const userRef = doc(db, "users", currentUser.uid);
                const userDoc = await getDoc(userRef);
                const userIsAdmin = currentUser.uid === ADMIN_ID;

                if (userDoc.exists()) {
                    const userData = userDoc.data();
                    setAccountStatus(userData.accountStatus || 'pending');
                    if (userIsAdmin && !userData.isAdmin) {
                        await updateDoc(userRef, { isAdmin: true, accountStatus: 'approved' });
                        setIsAdmin(true);
                        setAccountStatus('approved');
                    } else {
                        setIsAdmin(userData.isAdmin || false);
                    }
                } else {
                    await setDoc(userRef, {
                        displayName: currentUser.displayName,
                        email: currentUser.email,
                        photoURL: currentUser.photoURL,
                        isAdmin: userIsAdmin,
                        accountStatus: userIsAdmin ? 'approved' : 'pending',
                        createdAt: serverTimestamp()
                    });
                    setAccountStatus(userIsAdmin ? 'approved' : 'pending');
                    setIsAdmin(userIsAdmin);
                }
                setUser(currentUser);
            } else {
                setUser(null);
                setIsAdmin(false);
                setAccountStatus('loading');
            }
            setLoadingAuth(false);
        });
        return () => unsubscribe();
    }, []);

    if (loadingAuth) return <LoadingScreen message="Memuat Autentikasi..." />;
    if (!user) return <LoginScreen />;
    
    return (
        <div className="bg-slate-100 dark:bg-slate-900 text-slate-800 dark:text-slate-200 min-h-screen font-sans antialiased">
            {
                {
                    'approved': <TradingJournalLayout user={user} isAdmin={isAdmin} />,
                    'pending': <PendingApprovalScreen user={user} />,
                    'blocked': <BlockedScreen user={user} />
                }[accountStatus] || <LoadingScreen message="Memverifikasi status akun..." />
            }
        </div>
    );
}

